"use strict";
import Sprite from "openfl/display/Sprite";
import Stage from "openfl/display/Stage";
import Event from "openfl/events/Event";
import MouseEvent from "openfl/events/MouseEvent";
import Font from "openfl/text/Font";
import TextField from "openfl/text/TextField";
import TextFormat from "openfl/text/TextFormat";
import AssetLibrary from "openfl/utils/AssetLibrary";
import AssetManifest from "openfl/utils/AssetManifest";

var start = false;

class App extends Sprite {


	constructor () {

		super ();

		//this.resize ();
		this.stage.addEventListener (Event.RESIZE, this.resize);
		this.stage.addEventListener (MouseEvent.MOUSE_DOWN, this.mousedown);

	}


// Outras funções
// quando o stage é redimensionado

	resize = (event = null) => {

		if (start == true)
		{
		//stage.color= 0x000000;
		console.log(this.stage.stageWidth);
		console.log(this.stage.stageHeight);
		}
		else
		{
		console.log("Primeira vez");
		start = true;
		}
	}

	mousedown = (event) => {
		console.log("mouse down")
	}


}

var stage = new Stage (0, 0, 0x25346f, App);
document.body.appendChild (stage.element);



